.slider {
  width: 1000px;
  height: 430px;
  background-color: #e8e8e8;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Georgia;
}

.slider-rectangle {
  width: 960px;
  height: 70px;
  background-color: #ffffff;
  border-radius: 5px;
